import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, formValueSelector, reduxForm } from "redux-form";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class FormModalValidasiStok extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    this.setData();
  }
  setData() {
    this.props.change("bruto", this.props.bruto);
    this.props.change("noFaktur", this.props.noFaktur);
  }
  setSisa(data) {
    this.props.change("sisa", this.props.sisaHutang);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="col-lg-12">
          <div className="row">
            <div className="col-lg-4">
              <Field
                name="bruto"
                label="Bruto (Gr)"
                type="text"
                component={renderField}
                value="100"
                readOnly={true}
                onBlur={this.setData()}
              />
            </div>
            <div className="col-lg-4 d-none">
              <Field
                name="noFaktur"
                label="Netto (Gr)"
                type="text"
                component={renderField}
                value="100"
                readOnly={true}
              />
            </div>
            <div className="col-lg-4">
              <Field
                name="jumlah"
                label="Jumlah Bayar (Gr)"
                type="text"
                component={renderField}
                onChange={this.setSisa()}
              />
            </div>
            <div className="col-lg-4">
              <Field
                name="sisa"
                label="Sisa Hutang (Gr)"
                type="text"
                component={renderField}
              />
            </div>
            <div className="col-lg-12">
              <div className="text-right">
                <button className="btn btn-primary" type="submit">
                  Simpan
                </button>
              </div>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormModalValidasiStok = reduxForm({
  form: "FormModalValidasiStok",
  enableReinitialize: true,
})(FormModalValidasiStok);
const selector = formValueSelector("FormModalValidasiStok"); // <-- same as form name
export default connect((state) => {
  const { jumlah, bruto } = selector(state, "jumlah", "bruto");
  return {
    sisaHutang: (parseFloat(bruto || 0) - parseFloat(jumlah || 0)).toFixed(3),
  };
})(FormModalValidasiStok);
